<template>
  <div>
    <nav-bar>
      <div slot="left" @click="backclick" ><img src="~assets/img/common/back.svg" alt="" /></div>
      <div slot="center" class="title">
        <div v-for="(items,index) in titles"
         :key="items"
          class="title-item"
          :class="{active:currentIndex===index}"
          @click="titleclick(index)"
          >
          {{items}}
          </div>
        </div>
    </nav-bar>
    
  </div>
</template>

<script>
 import NavBar from 'components/common/navbar/NavBar'

 import {getDetail} from '../../network/detail'
export default {
  name: '',
  data () {
    return {
      message:'详情页:',
      titles:['商品','参数','评论','推荐'],
      currentIndex:0,
      iid:0
    };
  },
  components: {
    NavBar
  },
  created() {
    //1.获取Iid
    const iid=this.$route.query.iid

    //2.请求详细参数
    getDetail(iid).then(res=>{
      console.log(res)
    })

  },
  mounted () {},
  methods: {
  titleclick(index){
   this.currentIndex=index
  },
   backclick(){
     this.$router.go(-1)
   }
  }
}
</script>

<style  scoped>
.nav-bar .left img{
  padding-top:10px
}
.title{
  display: flex
}
.title-item{
  flex:1;
  
}
.active{
  color:pink
}

</style>